.speaker {
    position: relative;
    padding-top: 25px;

    &__container {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }
    &__photo {
        width: 145px;
        height: 200px;
        align-self: flex-end;
        margin-top: -21px;

        & img {
            width: 100%;
            height: 100%;
        }
    }
    &__about {
        max-width: 735px; 

        & .speaker__title {
            display: none;
        }
    }
    &__title {
        // margin-top: 32px;
        // margin-bottom: 56px;
    }
    &__discription {}
    &__name {
        @include font($fz: 1.8rem, $fc: $white, $fw: 600);
        background-image: linear-gradient(270deg, $blue, $primary-color);
        width: 288px;
        padding: 16px;
        position: relative;
        margin-bottom: 25px;
        margin-top: -20px;

        &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: -16px;
            left: 0;
            border-top: 16px solid $primary-color;
            border-right: 18px solid transparent;

        }
    }
    &__achievements {
        padding: 0;
        list-style: none;
    }
    &__achievement {
        display: flex;
        align-items: flex-start;
        padding-bottom: 16px;
        @include font($fz: 1.6rem, $fc: $dark-gray);
    }
    &__icon {
        margin-right: 15px;
        margin-top: 1px;
        width: 16px;
        height: 16px;
    }

    & .red-blok_left {
        width: 85px;
        height: 310px;
        border: none;
        background-color: #F5F8FE;
        top: 3%;
        left: 0%;
        z-index: -1;

        &::after {
            top: 15px;
            left: 15px;
        }
    }
}

@media screen and (min-width: 576px) {}
@media screen and (min-width: 768px) {
    .speaker {
        &__container {
            flex-direction: row;
            justify-content: space-between;
            position: relative;
        }
        &__name {
            position: absolute;
            left: 0;
            top: 60px;
            text-align: center;
            width: 316px;
        }
        &__photo {
            width: 300px;
            height: 460px;
            margin-top: 140px;
        }
        &__about {
            width: 50%;
            margin-top: -25px;
        }
        & .red-blok_left {
            width: 225px;
            height: 800px;
            top: 14%;
            left: -25%;
    
            &::after {
                top: 40px;
                left: 40px;
            }
        }
    }
}
@media screen and (min-width: 992px) {
    .speaker {
        padding-top: 95px;
        
        &__title {
            display: none;
        }
        &__about {
            // max-width: 735px;
            // width: auto; 
    
            & .speaker__title {
                display: block;
                margin-top: 32px;
                margin-bottom: 56px;
            }
        }
        &__photo {
            width: 540px;
            height: 750px;
            // margin-left: 65px;
            margin-top: 0;
        }
        &__name {
            position: relative;
            top: 0;
            text-align: left;
            font-size: 3.2rem;
            width: 100%;
            // padding: 21px 0 30px 32px;
            margin-top: 0;
            margin-bottom: 40px;

            &:after {
                bottom: -19px;
                border-top-width: 19px;
            }
        }
        & .red-blok_left {
            height: 1100px;
            top: 12%;
            left: -30%;
            &::after {
                top: 40px;
                left: 50px;
            }
        }
        &__achievement {
            // font-size: 1.8rem;
            align-items: center;
        }
    }
}
@media screen and (min-width: 1200px) {
    .speaker {
        &__name {
            padding: 21px 0 30px 32px;
        }
        &__photo {
            height: 830px;
        }
        &__achievement {
            font-size: 1.8rem;
        }
        & .red-blok_left {
            width: 306px;
            top: -5%;
            left: -8%;
        }
        &__about {
            max-width: 736px; 
        }
    }
}
@media screen and (min-width: 1440px) {
    .speaker {
        & .red-blok_left {
            left: -4%;
        }
    }
}
@media screen and (min-width: 1920px) {
    .speaker {
        & .red-blok_left {
            left: 5%;
        }
    }
}